// Mixins specific to this page
@mixin settings-arrow( $width, $direction: '' ) {
    // We will calculate the ratio until aspect-ratio gets better support
    $aspect_ratio: calc( 4 / 5 ); // 0.8;

    content: '';

    width: $width;
    height: calc( $width * $aspect_ratio );

    background: url( 'img/arrow-settings.svg' ) no-repeat top left;
    background-size: contain;

    position: absolute;
    z-index: -1;

    // Define position
    @if $direction == 'bottom' {
        transform: rotate(-90deg);
    }
}

.uap-settings {
    /* Set the content sticky */
    position: sticky;
    top: var(--uap-wordpress-admin-bar-height);

    font-family: var(--uap-font-family);
    font-size: var(--uap-font-size--body-small);
    line-height: var(--uap-line-height--body-small);
    
    height: calc( 100vh - var(--uap-wordpress-admin-bar-height) );
    display: flex;
    flex-direction: column;

    background-color: #fff;

    &-header {
        flex: 0 0 auto;

        padding: 30px 30px 20px;

        &__title {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-regular);
            line-height: var(--uap-line-height--header-regular);
            font-weight: 600;
            color: var(--uap-font-color);
            margin: 0;
        }
    }

    &-content {
        flex: 1 1 auto;
        // Flex overflow fix
        min-height: 1px;

        // Make the tabs take as much space as possible
        uo-tabs {
            height: 100%;
        }

        // Main tabs
        &-main-tabs {
        }
    }

    &-panel {
        width: 100%;
        height: 100%;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        display: flex;
        flex-direction: column;
        min-height: 0px;

        &-top {
            flex: 1 1 auto;
            overflow: auto;

            padding: 20px;
        }

        &-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;

            flex: 0 0 auto;
            padding: 15px 20px;
            box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.05), inset 0px 1px 0px var(--uap-border-color-gray);

            position: relative;

            &-right {
                uo-button:not(:first-child) {
                    margin-left: 10px;
                }
            }

            // Arrow
            &[has-arrow]::before {
                @include settings-arrow( 80px, 'bottom' );

                bottom: calc( 100% + 30px );
                left: 30px;
            }
        }

        &-title {
            & > uo-icon {
                margin-right: 10px;
                font-size: 20px;
                line-height: 1;
            }

            display: flex;
            align-items: center;

            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-small);
            line-height: var(--uap-line-height--header-small);
            font-weight: 600;
            color: var(--uap-font-color);
            margin: 0;

            & uo-pro-tag {
                margin-left: 10px;
            }
        }

        &-content {
            margin-top: 15px;

            font-size: var(--uap-font-size--body-small);
            line-height: var(--uap-line-height--body-small);

            &-subtitle {
                display: flex;
                align-items: center;

                font-family: var(--uap-font-family--header);
                font-size: var(--uap-font-size--header-xsmall);
                line-height: var(--uap-line-height--header-xsmall);
                font-weight: 600;
                color: var(--uap-font-color);

                & uo-pro-tag {
                    margin-left: 10px;
                }
            }

            &-separator {
                padding: 20px 0;
                position: relative;

                &::before {
                    content: '';

                    width: 100%;
                    height: 1px;

                    display: block;

                    background: #e4e4e4;
                }
            }

            p,
            &-paragraph {
                font-size: var(--uap-font-size--body-small);
                line-height: var(--uap-line-height--body-small);
                color: var(--uap-font-color);
                font-weight: 400;
                margin: 10px 0;

                &--subtle {
                    color: var(--uap-font-color-secondary);
                }
            }

            &-buttons {
                margin-top: 15px;
                display: flex;

                & uo-button:not(:last-child) {
                    margin-right: 15px;
                }
            }
        }

        &-user {
            display: flex;
            align-items: center;

            &__avatar {
                display: flex;
                justify-content: center;
                align-items: center;

                width: 32px;
                height: 32px;

                border-radius: 150px;
                background-color: var(--uap-alert-info-background);
                color: #000;

                font-size: var(--uap-font-size--body-large);
                line-height: var(--uap-line-height--body-large);
                font-weight: 400;

                overflow: hidden;

                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }

            &-info {
                margin-left: 10px;

                &__main {
                    display: flex;
                    align-items: center;


                    font-size: var(--uap-font-size--body-xsmall);
                    line-height: var(--uap-line-height--body-xsmall);
                    font-weight: 500;
                    color: var(--uap-font-color);

                    & uo-icon {
                        margin-left: 5px;
                        font-size: 12px;
                        line-height: 0;
                    }
                }

                &__additional {

                    font-size: var(--uap-font-size--body-xxsmall);
                    line-height: var(--uap-line-height--body-xxsmall);
                    font-weight: 400;
                    color: var(--uap-font-color);
                }
            }

        }

        /* Specific special panels */
        &-placeholder {
            height: 100vh;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            &[has-arrow]::before {
                @include settings-arrow( 100px );

                top: 30px;
                left: 30px;
            }

            /* Exceptions */
            & > uo-icon {
                font-size: 60px;
                color: var(--uap-color-gray);
            }

            & .uap-settings-panel-title {
                flex-direction: column;
                margin-top: 30px;
                color: #000;
            }

            & .uap-settings-panel-content {
                max-width: 500px;
                margin-top: 0;

                & .uap-settings-panel-content-paragraph {
                    text-align: center;

                    font-size: var(--uap-font-size--body-regular);
                    line-height: var(--uap-line-height--body-regular);
                }

                & .uap-settings-panel-content-buttons {
                    justify-content: center;
                }
            }
        }
    }
}

/**
 * WP exceptions
 */

.uo-recipe_page_uncanny-automator-config {
    // Remove default padding added to the wpcontent wrapper
    #wpcontent {
        padding-left: 0 !important;
    }

    // Hide "Screen options"
    #screen-meta-links {
        display: none !important;
    }

    // Remove float of the wpbody wrapper so the children can be sticky
    #wpbody-content {
        float: none !important;
        padding-bottom: 0 !important;
    }

    // Hide WordPress footer. We need as much space as possible
    #wpfooter {
        display: none !important;
    }

    // Hide all notices not related to Automator
    .notice {
        display: none !important;
    }
}

/**
 * Sign in with Google
 */ 

.uap-settings-button-google {
    --uap-button-style-custom-border-color: #dadce0;
    --uap-button-style-border-radius: 4px;
    --uap-button-style-custom-color: #3c4043;

    --uap-button-style-padding-left: 12px;
    --uap-button-style-padding-right: 12px;

    --uap-button-style-box-shadow: none;
    --uap-button-style-focus-box-shadow: none;

    --uap-button-style-font-size: 14px;
    --uap-button-style-line-height: 1;

    --uap-button-style-display: flex;
    --uap-button-style-align-items: center;

    --uap-button-style-height: 38px;

    uo-icon {
        font-size: 18px;
        margin-right: 8px;
    }

    // Hover
    --uap-button-style-custom-hover-color: #3c4043;
    --uap-button-style-custom-hover-background: rgba(66,133,244,.04);

    &:hover {
        --uap-button-style-custom-border-color: #d2e3fc;
    }

    &:active {
        --uap-button-style-custom-hover-background: rgba(66,133,244,.1);
    }
}