/**
 * This file only contains SCSS mixins to use an icon as pseudo-element
 * Don't use the unicode to add the icon, use the CSS variables, which
 * are defined in style.scss
 */

@mixin icon {
	font-size: 1em;
	font-family: "Uncanny Automator Icons" !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin icon-loading( $translate: translate(-50%, -50%) ) {
	// Define the name of the keyframes animation
	$keyframes_name: unique_id() + "-rotate";

	// Add icon
	@include icon;
	content: var(--uap-icon-loading);

	// Style it
	color: var(--uap-font-color);

	// Center it
	position: absolute;
	top: 50%;
	left: 50%;

	// Add animation
	animation: #{ $keyframes_name } 2s linear infinite;
	@keyframes #{ $keyframes_name } {
		from {
			transform: $translate rotate(0deg);
		}
		to {
			transform: $translate rotate(360deg);
		}
	}
}
