.uap-upgrade {
    max-width: 800px;
    margin: 0 auto;
    padding: 60px;

    @media screen and (max-width: 768px){
        padding: 60px 20px;
    }

    &-wrapper {
        background-color: #fff;
    }

    &-header {
        

        &__logo {
            width: 300px;

            img {
                width: 100%;
                height: 100%;

                object-fit: cover;
                object-position: center;
            }
        }

        &__title {
            color: #000;
            font-size: var(--uap-font-size--header-regular);
            line-height: var(--uap-line-height--header-regular);
            font-weight: 600;
            margin: 0;
            padding: 20px 0;
        }

        &__content {
            color: var(--uap-font-color-secondary);
            font-size: var(--uap-font-size--body-small);
            line-height: var(--uap-line-height--body-small);
            padding-bottom: 20px;
        }
    }

    &-comparison-table {

        width: 80%;
        
        @media screen and (max-width: 768px){
            width: 100%;
        }

        margin: 20px 0;

        thead {
            tr { 
                th {
                    padding: 10px 5px;
                    font-size: var(--uap-font-size--header-xsmall);
                    font-weight: 500;
                }
            }
        }

        tbody {

            tr {
                padding: 10px 5px;
            }

            th {
                text-align: left;
            }

            td {
                padding: 12px 5px 12px 10px;
                text-align: center;
                font-size: var(--uap-font-size--body-large);

                uo-icon#check {
                    color: green;
                }

                uo-icon#times {
                    color: red;
                }
            }
        }

    }

    &-body {
        &-row--feature-category {
            background-color: #f3f3f3;

            th {
                font-size: var(--uap-font-size--body-regular);
                line-height: var(--uap-line-height--body-regular);
                font-weight: 500;
            }
        }

        &-row--feature {
            th {
                font-size: var(--uap-font-size--body-regular);
                line-height: var(--uap-line-height--body-regular);
                font-weight: 400;
                color: var(--uap-font-color-secondary);

                a {
                    uo-icon[id=external-link] {
                        font-size: .9em;
                    }

                    &:hover {
                        text-decoration: none;
                    }
                }

                uo-icon[integration] {
                    font-size: .9em;
                    margin: 0 6px 0 2px;
                }
            }
        }
        
        &-row--feature-category th,
        &-row--feature th {
            padding: 12px 5px 12px 10px;
        }
    }

    &__button a {
        background-color: var(--uap-color-green);
        color: #fff !important;

        padding: 12px 25px;
        border-radius: 150px;

        display: inline-block;

        font-size: var(--uap-font-size--body-regular);
        line-height: var(--uap-line-height--body-regular);
        font-weight: 500;
        
        transition: background-color 150ms ease-in-out;

        &:hover {
            background-color: #579f4a;
            text-decoration: none;
        }
    }
}