// Import field types
@import "./types/text.scss";
@import "./types/switch.scss";

.uap-field {
    position: relative;
    width: 100%;

	&-icon {
	    position: absolute;
	    z-index: 100;
	    left: 15px;
	    top: 50%;
	    transform: translateY(-50%);

	    font-size: var(--uap-font-size--body-small) !important;
	    line-height: var(--uap-line-height--body-small) !important;
	    font-weight: 600 !important;
	}

	& > label {
		font-size: var(--uap-font-size--body-small);
	    line-height: var(--uap-line-height--body-small) !important;
	    color: var(--uap-font-color) !important;

	    display: block;

	    font-weight: 500;

	    margin-bottom: 8px;
	}

	&-description {
		font-size: var(--uap-font-size--body-xsmall);
		line-height: var(--uap-line-height--body-xsmall);
		color: var(--uap-font-color-secondary);

		margin-top: 8px;
	}

	&--has-icon {
		padding: 10px 12px 10px 40px !important;
	}

}
