// Import breakpoints
@import '@automator/components/icon/mixin.scss';

.uap-dropdown {
	$section: &;

	cursor: pointer;
	position: relative;

	&-toggle {
		position: relative;

		padding-right: calc(12px + var(--uap-spacing-between-elements-small));

		&:after {
			@include icon;
			content: var(--uap-icon-angle-down);

			transition: var(--uap-transition);
			transition-property: transform;

			font-size: 16px;
			width: 11px;
			height: 16px;

			position: absolute;
			right: 0;
			top: calc(50% + 1px);
			transform: translateY(-50%);
			text-align: center;

			#{ $section }--open & {
				transform: translateY(-50%) rotate(180deg);
			}
		}
	}

	&-menu {
		border-radius: var(--uap-border-radius);
		box-shadow: var(--uap-shadow-dropdown);
		transition: var(--uap-transition);

		transition-property: opacity, top;

		opacity: 0;
		top: calc(100% + 15px);
		height: 0;
		overflow: hidden;

		background: #fff;
		width: 200px;
		position: absolute;
		right: -10px;
		z-index: 10;

		#{ $section }--open & {
			height: auto;
			opacity: 1;
			top: calc(100% + 20px);
			overflow: visible;
		}

		&:before {
			--uap-caret-size: 8px;

			border: var(--uap-caret-size) solid transparent;
			top: calc(var(--uap-caret-size) * -2);

			content: "";
			position: absolute;
			left: 50%;
			width: 0;
			height: 0;
			border-bottom-color: #fff;
		}

		#{ $section }:not(#{ $section }--caret-left):not(#{ $section }--caret-right ) & {
			&,
			&:before {
				left: 50%;
				transform: translateX(-50%);
			}
		}

		#{ $section }--caret-left & {
			left: -10px;

			&:before {
				left: 10px;
			}
		}

		#{ $section }--caret-right & {
			right: -10px;

			&:before {
				right: 10px;
				left: auto;
			}
		}
	}

	&-item {
		color: var(--uap-font-color) !important;
		font-size: var(--uap-font-size--body-small);
		line-height: var(--uap-line-height--body-small);
		transition: var(--uap-transition);

		transition-property: color;
		padding: 7px 10px;
		text-decoration: none;
		display: block;

		&:not(:last-child) {
			border-bottom: 1px solid var(--uap-border-color-gray);
		}

		&:hover {
			color: var(--uap-color-blue);
		}

		.uap-icon {
			margin-left: 2px;
		}
	}
}