/**
 * This file is always included in the bundle.min.css so the font with the icons
 * and the variables are available for all components
 */

// Import icons
@import "./icons.scss";

:root {
	@each $id, $glyph in $icons {
		--uap-icon-#{ $id }: "#{ $glyph }";
	}
}

@font-face {
	font-family: "Uncanny Automator Icons";
	src:
		url(./fonts/uncanny-automator-icons.ttf)  format("truetype"),
		url(./fonts/uncanny-automator-icons.woff) format("woff"),
		url(./fonts/uncanny-automator-icons.svg)  format("svg");
	font-weight: normal;
	font-style: normal;
}
