.uap-field {
	$checkbox: & input;

	&-switch {
		margin-bottom: 0 !important;

		// Hide the real checkbox
		input {
			display: none !important;
		}

		display: inline-flex !important;
		align-items: center;

		&__handle {
			width: 26px;
			height: 12px;

			border: 2px solid var(--uap-color-gray);
			background: transparent;

			border-radius: 150px;

			position: relative;

			transition: border 150ms ease-in-out, background 150ms ease-in-out;

			// Checked
			#{ $checkbox }:checked ~ & {
				border-color: var(--uap-color-green);
				background: var(--uap-color-green);
			}

			&:after {
				content: '';

				background: var(--uap-color-gray);

				position: absolute;

				width: 8px;
				height: 8px;

				border-radius: 50%;

				top: 2px;
				left: 3px;

				transition: left 150ms ease-in-out, background 150ms ease-in-out;

				// Checked
				#{ $checkbox }:checked ~ & {
					left: 15px;

					background: #fff;
				}
			}

			// Disabled
			#{ $checkbox }:disabled ~ & {
				opacity: .7;
				pointer-events: none;
				cursor: default;
			}
		}

		&__label {
			margin-left: 5px;

			font-size: var(--uap-font-size--body-small);
			line-height: var(--uap-line-height--body-small) !important;

			color: var(--uap-font-color-secondary);

			display: none;

			font-weight: 600;

			margin-left: 6px;

			user-select: none;

			&--on {
				color: var(--uap-font-color-green);
			}

			// Checked
			#{ $checkbox }:checked ~ &--on {
				display: block;
			}

			// Not checked
			#{ $checkbox }:not(:checked) ~ &--off {
				display: block;
			}

			// Disabled
			#{ $checkbox }:disabled ~ & {
				opacity: .7;
				pointer-events: none;
				cursor: default;
			}
		}
	}
}
