:root {
	--uap-context-menu-width: 200px;
	--uap-context-menu-background: #e9e4e7;
	--uap-context-menu-border-color: rgba(0, 0, 0, .1);

	--uap-context-menu-group-separator-color: #d2cdd0;
	--uap-context-menu-group-title-padding: 4px 8px;

	--uap-context-menu-item-padding: 6px 8px;
}

.uap-context-menu {
	width: var( --uap-context-menu-width );

	font-family: var( --uap-font-family );
	color: var( --uap-font-color );
	font-size: var( --uap-font-size--body-xsmall );

	border-radius: var( --uap-border-radius );
	box-shadow: var( --uap-shadow-dropdown );

	background: var( --uap-context-menu-background );
	border-color: var( --uap-context-menu-border-color );

	border-width: 1px;
	border-style: solid;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 2147483647;
	display: none;
	overflow: hidden;

	&-group {
		padding: 5px;

		&:not(:last-child) {
			border-bottom: 1px solid var( --uap-context-menu-group-separator-color );
		}

		&-title {
			padding: var( --uap-context-menu-group-title-padding );
			color: var( --uap-font-color-secondary );
			font-size: var( --uap-font-size--body-xxsmall );

			font-weight: 400;
		}

		&-item {
			padding: var( --uap-context-menu-item-padding );
			background: var( --uap-context-menu-background );
			border-radius: var( --uap-border-radius );

			font-weight: 500;

			transition: background 150ms ease-in-out, color 150ms ease-in-out;
			cursor: pointer;

			&:not(&--disabled):hover {
				background: var( --uap-color-blue );
				color: #fff;
			}

			&--disabled {
				opacity: .5;
				cursor: not-allowed;
			}
		}
	}
}