// Import breakpoints
@import '@automator/utilities/breakpoint/style.scss';

:root {
	--uap-dashboard-section-content-padding: 20px;
	--uap-dashboard-box-padding: 20px;

	/* Progress bar */
	--uap-dashboard-progress-height: 5px;
	--uap-dashboard-progress-background: #f3f3f3;
	--uap-dashboard-progress-bar: var(--uap-color-blue);
	--uap-dashboard-progress-bar-warning: #ffb72d;
	--uap-dashboard-progress-bar-success: var(--uap-color-green);
}

.uap-dashboard {
	font-size: var(--uap-font-size--body-regular);
	line-height: var(--uap-line-height--body-regular);
	padding-top: calc(var(--uap-spacing-between-sections) / 3);

	&-header {
		padding-bottom: calc(var(--uap-spacing-between-sections) / 3);

		width: 100%;

		display: flex;
		justify-content: space-between;
		align-items: center;

		&__title {
			font-family: var(--uap-font-family--header) !important;
			font-size: var(--uap-font-size--header-small) !important;
			line-height: var(--uap-line-height--header-small) !important;

			font-weight: 700 !important;
		}

		&-user {
			--uap-user-avatar-size: 26px;

			padding-left: calc(var(--uap-user-avatar-size) + var(--uap-spacing-between-elements));

			position: relative;

			&__avatar {
				width: var(--uap-user-avatar-size);
				height: var(--uap-user-avatar-size);
				margin-left: calc(calc(var(--uap-user-avatar-size) + var(--uap-spacing-between-elements)) * -1);

				border: 2px solid #b9b9b9;
				border-radius: 50%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				overflow: hidden;

				display: flex;
				align-items: center;
				justify-content: center;

				.uap-dashboard--has-site-connected & {
					width: calc(var(--uap-user-avatar-size) + 2px);
					height: calc(var(--uap-user-avatar-size) + 2px);

					border: 0;
				}

				uo-icon[id="user"] {
					color: #838383;
					font-size: 16px;
				}

				img {
					width: 100%;
					height: 100%;

					object-fit: cover;
					object-position: center;
				}
			}

			&__name {
				font-weight: 500;
			}
		}
	}

	&-connect-site-integration {
		--illustration-width: 110px;

		margin-bottom: calc(var(--uap-spacing-between-sections) / 2);

		.uap-notice {
			padding-left: calc(calc(var(--illustration-width) + var(--uap-box-padding)) + var(--uap-box-padding));
			position: relative;

			&::before {
				width: var(--illustration-width);
				height: calc(var(--illustration-width) * 1.3078);
				top: var(--uap-box-padding);
				left: var(--uap-box-padding);

				content: "";

				background: url( @pages/global/img/robot-site-not-connected.svg ) no-repeat center;
				background-size: contain;

				position: absolute;
			}
		}
	}

	&-section {
		padding: var(--uap-dashboard-section-content-padding);
		border-radius: var(--uap-border-radius);
		margin-bottom: calc(var(--uap-spacing-between-sections) / 2);

		background: #fff;

		&__title {
			font-family: var(--uap-font-family--header);
			font-size: var(--uap-font-size--header-small);
			line-height: var(--uap-line-height--header-small);
			padding-bottom: calc(var(--uap-spacing-between-sections) / 3);

			font-weight: 700;
		}

		&__content {
			display: grid;
			grid-template-rows: 1fr;
			gap: var(--uap-dashboard-section-content-padding);
			grid-template-areas: ". . .";
		}
	}

	&-box {
		border-radius: var(--uap-border-radius);
		box-shadow: var(--uap-box-shadow);

		background: #fff;
		display: flex;
		flex-direction: column;

		&-header {
			padding: var(--uap-dashboard-box-padding);
			padding-bottom: calc(var(--uap-dashboard-box-padding) / 2);

			flex: 0 0 auto;

			&--no-padding {
				padding: 0;
			}

			&__title {
				font-family: var(--uap-font-family--header);
				font-size: var(--uap-font-size--body-large);

				font-weight: 800;
			}
		}

		&-progress {
			background: var(--uap-dashboard-progress-background);
			height: var(--uap-dashboard-progress-height);
			border-top-left-radius: var(--uap-border-radius);
			border-top-right-radius: var(--uap-border-radius);

			width: 100%;
			overflow: hidden;

			&-bar {
				background: var(--uap-dashboard-progress-bar);
				transition: var(--uap-dashboard-transition);

				transition-property: width;
				height: 100%;
				box-shadow: 0 0 0 3px #fff;
				border-radius: 150px;

				.uap-dashboard-box-progress--warning & {
					background: var(--uap-dashboard-progress-bar-warning);
				}

				.uap-dashboard-box-progress--success & {
					background: var(--uap-dashboard-progress-bar-success);
				}
			}
		}

		&-content {
			padding: var(--uap-dashboard-box-padding);

			flex: 1 1 auto;
			display: flex;
			flex-direction: column;
			justify-content: center;

			position: relative;

			&--top {
				justify-content: flex-start;
			}

			&--has-scroll {
				&::after {
					height: calc(var(--uap-dashboard-box-padding) * 2);

					content: "";

					position: absolute;
					bottom: 0;
					left: 0;
					right: 0;

					background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
				}
			}

			&-scroll {
				left: var(--uap-dashboard-box-padding);
				right: calc(var(--uap-dashboard-box-padding) / 2);
				padding-right: calc(var(--uap-dashboard-box-padding) / 2);
				padding-bottom: calc(var(--uap-dashboard-box-padding) * 2);

				overflow: auto;

				display: flex;
				flex-direction: column;
				position: absolute;

				top: 0;
				bottom: 0;

				&::-webkit-scrollbar {
					width: var(--uap-scrollbar-width);
					background-color: var(--uap-scrollbar-background);

					border-radius: 150px;
				}

				&::-webkit-scrollbar-thumb:vertical {
					min-height: var(--uap-scrollbar-width);
					background: var(--uap-scrollbar-thumb-background);

					border-radius: 150px;
					background-clip: padding-box;
					border: 2px solid rgba(0, 0, 0, 0);
				}
			}

			.uap-dashboard-box-header:not(.uap-dashboard-box-header--no-padding) + & {
				padding-top: calc(var(--uap-dashboard-box-padding) / 2);

				&-scroll {
					top: calc(var(--uap-dashboard-box-padding) / 2);
				}
			}

			&-number {
				font-size: var(--uap-font-size--header-xlarge);
				line-height: var(--uap-line-height--header-xlarge);
				font-family: var(--uap-font-family--header);

				width: 100%;
				font-weight: 800;
				text-align: center;
			}

			&-below-number {
				font-size: var(--uap-font-size--body-large);
				line-height: var(--uap-line-height--body-large);
				font-family: var(--uap-font-family--header);
				color: var(--uap-font-color-secondary);

				width: 100%;
				font-weight: 600;
				text-align: center;
			}

			&-label {
				font-size: var(--uap-font-size--body-large);
				margin-top: calc(var(--uap-spacing-between-elements) * 2);

				width: 100%;
				font-weight: 500;
				text-align: center;

				&--reduced-margin {
					margin-top: var(--uap-spacing-between-elements-small);
				}
			}

			&-below-label {
				margin-top: calc(var(--uap-spacing-between-elements) * 2);

				width: 100%;
				font-weight: 500;
				text-align: center;

				&--warning {
					color: var(--uap-font-color-warning);
				}

				&--secondary {
					color: var(--uap-font-color-secondary);
				}
			}
		}

		&-list {
			width: 100%;
			padding: 0;
			margin: 0;

			li {
				width: 100%;
				padding: 0;
				margin: 0;

				a {
					display: inline-block;
					padding: 3px 0;
				}
			}

			&--plus-sign {
				li {
					a {
						width: 100%;
						padding-right: 25px;
						position: relative;

						&::after {
							content: "\f067";

							position: absolute;
							top: 9px;
							right: 0;

							font-size: 13px;

							opacity: 0.8;

							font-family: "Uncanny Automator Icons" !important;
							speak: none;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							line-height: 1;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
				}
			}
		}

		&-footer {
			padding: calc(var(--uap-dashboard-box-padding) / 2) var(--uap-dashboard-box-padding);
			border-top: 1px solid var(--uap-border-color-gray);
			font-size: var(--uap-font-size--body-small);
			line-height: var(--uap-line-height--body-small);

			flex: 0 0 auto;

			a {
				text-decoration: none;
				color: var(--uap-color-blue);

				&:hover {
					color: var(--uap-color-blue);
                }
			}
		}
	}

	&-videos {
		margin-top: calc(var(--uap-spacing-between-elements-small) * -1);

		display: flex;
		flex-direction: column;

		a {
			text-decoration: none;
		}
	}

	&-video {
		padding: var(--uap-spacing-between-elements-small) 0;

		display: flex;
		align-items: flex-start;
		transition: opacity 150ms ease-in-out;

		&:hover {
			opacity: 0.8;
		}

		&__thumbnail {
			position: relative;
			flex: 0 0 auto;
			width: 94px;
			height: auto;
			margin-top: 4px;

			img {
				border-radius: var(--uap-border-radius);

				display: block;
				width: 100%;
				height: 100%;
			}

			&::before {
				content: var(--uap-icon-play);

				font-size: calc(1em - 2px);
				font-family: "Uncanny Automator Icons" !important;
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				color: #fff;

				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 10;
			}

			&::after {
				border-radius: var(--uap-border-radius);

				content: "";
				width: 100%;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.2);

				position: absolute;
				top: 0;
				left: 0;
				z-index: 5;
			}
		}

		&__title {
			font-size: var(--uap-font-size--body-small);
			line-height: var(--uap-line-height--body-small);
			color: var(--uap-font-color);
			padding: 0 0 0 var(--uap-spacing-between-elements);

			flex: 1 1 auto;
		}

		&__subtitle {
			font-size: var(--uap-font-size--body-xsmall);
			line-height: var(--uap-line-height--body-xsmall);
			color: var(--uap-font-color-secondary);

			padding-top: 2px;
		}
	}
}

/* "Credits" */

#uap-dashboard-credits {
	.uap-dashboard-section__content {
		grid-template-columns: 1fr 1fr 2fr;
		grid-template-rows: 1fr;
		grid-template-areas: "Credits-left Credits-faq Credits-recipes";
	}

	#uap-dashboard-credits-left {
		grid-area: Credits-left;
	}
	#uap-dashboard-credits-faq {
		grid-area: Credits-faq;
	}
	#uap-dashboard-credits-recipes {
		grid-area: Credits-recipes;
	}

	#uap-dashboard-credits-left .uap-dashboard-box-content {
		min-height: 220px;
	}

	#uap-dashboard-credits-faq .uap-dashboard-box-content {
		min-height: 270px;
	}
}

@media screen and (max-width: $breakpoint_small_desktop) {
	#uap-dashboard-credits .uap-dashboard-section__content {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		grid-template-areas:
			"Credits-left Credits-faq"
			"Credits-recipes Credits-recipes";
	}
}

@media screen and (max-width: $breakpoint_mobile) {
	#uap-dashboard-credits .uap-dashboard-section__content {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr;
		grid-template-areas:
			"Credits-left"
			"Credits-faq"
			"Credits-recipes";
	}
}

#uap-dashboard-credits-left-total {
	font-weight: 800;
}

#uap-dashboard-credits-faq {}

#uap-dashboard-credits-faq .uap-dashboard-box-list a {
	color: var(--uap-font-color-secondary);
}

#uap-dashboard-credits-recipes {}

#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes__no-recipes {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-cell--recipe-name {
	a {
		text-decoration: none;
	}
}
#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-cell--allowed-completions {}
#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-cell--completed-runs {}

#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-allowed-completions--per-user,
#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-allowed-completions--total {
	font-weight: 500;
	font-size: 14px;
}

#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-allowed-completions--per-user span,
#uap-dashboard-credits-recipes .uap-dashboard-credits-recipes-table-allowed-completions--total span {
	font-weight: 400;
}

/* Learn */

.uap-dashboard-learn {
	&-videos {
		grid-area: Learn-videos;
	}

	&-knowledge-base {
		grid-area: Learn-knowledge-base;

		&__number-of-articles {
			font-size: var(--uap-font-size--body-xsmall);
			line-height: var(--uap-line-height--body-xsmall);
			color: var(--uap-font-color-secondary);
		}

		.uap-dashboard-box-content {
			min-height: 275px;
		}
	}

	&-featured-integrations {
		grid-area: Learn-featured-integrations;
	}

	.uap-dashboard-section__content {
		grid-template-columns: 3fr 2fr 2fr;
		grid-template-rows: 1fr;
		grid-template-areas: "Learn-featured-integrations Learn-knowledge-base Learn-videos";

		@media screen and (max-width: $breakpoint_small_desktop) {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1fr 1fr;
			grid-template-areas:
				"Learn-featured-integrations Learn-knowledge-base"
				"Learn-videos .";
		}

		@media screen and (max-width: $breakpoint_mobile) {
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 1fr 1fr;
			grid-template-areas:
				"Learn-featured-integrations"
				"Learn-knowledge-base"
				"Learn-videos";
		}
	}
}