[uap-tooltip] {
	$tooltip: &;
	$flow: 'uap-tooltip-flow';

	position: relative;

	&:before,
	&:after {
		text-transform: none;
		font-size: 13px;
		font-weight: 500;
		line-height: 1;
		user-select: none;
		pointer-events: none;
		position: absolute;
		display: none;
		opacity: 0;
	}

	&:hover:before,
	&:hover:after {
		display: block;
	}

	&:before {
		content: '';

		border: 5px solid transparent;
		z-index: 1001;
	}

	&:after {
		content: attr(uap-tooltip);

		width: 100%;
		text-align: center;
		min-width: 80px;
		max-width: 21em;
		padding: 4px 10px 6px 10px;
		line-height: 1.4;
		border-radius: 4px;
		box-shadow: 0 1em 2em -0.5em rgba(0, 0, 0, 0.35);
		background: var(--uap-tooltip-background);
		color: #fff;
		z-index: 1000;
	}

	// Direction: up (default)
	&:not([#{ $flow }]),
	&[#{ $flow }^="up"] {
		&:before {
			bottom: calc(100% + 5px);
			border-bottom-width: 0;
			border-top-color: var(--uap-tooltip-background);
		}

		&:after {
			bottom: calc(100% + 10px);
		}

		&:before,
		&:after {
			left: 50%;
			transform: translate(-50%, -0.5em);
			animation: uap-tooltips-vert 300ms ease-out forwards;
		}
	}

	// Direction: down
	&[#{ $flow }^="down"] {
		&:before {
			top: 100%;
			border-top-width: 0;
			border-bottom-color: #333;
		}

		&:after {
			top: calc(100% + 5px);
		}

		&:before,
		&:after {
			left: 50%;
			transform: translate(-50%, 0.5em);
			animation: uap-tooltips-vert 300ms ease-out forwards;
		}
	}

	// Direction: left
	&[#{ $flow }^="left"] {
		&:before {
			top: 50%;
			border-right-width: 0;
			border-left-color: #333;
			left: calc(0em - 10px);
			transform: translate(-0.5em, -50%);
		}

		&:after {
			min-width: 80px;
			top: 50%;
			right: calc(100% + 10px);
			transform: translate(-0.5em, -50%);
		}

		&:before,
		&:after {
			animation: uap-tooltips-horz 300ms ease-out forwards;
		}
	}

	// Direction right
	&[#{ $flow }^="right"] {
		&:before {
			top: 50%;
			border-left-width: 0;
			border-right-color: #333;
			right: calc(0em - 10px);
			transform: translate(0.5em, -50%);
		}

		&:after {
			top: 50%;
			left: calc(100% + 10px);
			transform: translate(0.5em, -50%);
		}

		&:before,
		&:after {
			animation: uap-tooltips-horz 300ms ease-out forwards;
		}
	}
}

@keyframes uap-tooltips-vert {
	to {
		opacity: 1;
		transform: translate(-50%, 0);
	}
}

@keyframes uap-tooltips-horz {
	to {
		opacity: 1;
		transform: translate(0, -50%);
	}
}