$breakpoint_small_desktop: 1366px;
$breakpoint_mobile: 768px;

:root {
	--uap-font-family: -apple-system, system-ui, blinkmacsystemfont, "Segoe UI", roboto, "Helvetica Neue", arial, sans-serif;
	--uap-border-radius: 10px;
	--uap-box-padding: 20px;
	--uap-line-height: 1.4;

	/* Colors */

	--uap-color-blue: #0790e8;
	--uap-color-blue-rgb: 7, 144, 232; // Useful for applying alpha

	--uap-color-green: #6bc45a;

	--uap-color-red: #e94b35;
	--uap-color-red-rgb: 233, 75, 53;

	--uap-color-gray: #9e9e9e;
	--uap-color-gray-rgb: 158, 158, 158; // Useful for applying alpha

	--uap-warning-background: #fdf8d9;
	--uap-error-background: #f9e2e2;
	--uap-success-background: #ecf7ed;
	--uap-info-background: #e8f2ff;

	// Buttons
	--uap-button-transparent-hover-background: #dfdfdf;

	// Alert
	--uap-alert-default-background: #f3f3f3;
	--uap-alert-warning-background: var(--uap-warning-background);
	--uap-alert-error-background: var(--uap-error-background);
	--uap-alert-success-background: var(--uap-success-background);
	--uap-alert-info-background: var(--uap-info-background);
	--uap-alert-white-background: #fff;
	--uap-alert-padding: 15px;
	--uap-alert-small-padding: 10px;
	--uap-alert-xsmall-padding: 8px;

	// Dropdowns
	--uap-dropdown-border-color: #c7c7c7;
	--uap-dropdown-caret-size: 7px;

	// Chips
	--uap-chip-padding-left-right: 10px;
	--uap-chip-padding-top-bottom: 2px;

	--uap-chip-padding-left-right-small: 8px;
	--uap-chip-padding-top-bottom-small: 1px;

	--uap-chip-color-default-border: #b7b7b7;
	--uap-chip-color-default-fill: #ebebeb;

	--uap-chip-color-info-border: #0288d1;
	--uap-chip-color-info-fill: #d2eaff;

	--uap-chip-color-warning-border: #f4a568;
	--uap-chip-color-warning-fill: #ffe9ac;

	// Fields
	--uap-field-border-color: #cdcdcd;
	--uap-field-background-disabled: #ebebeb;

	// Dialog
	--uap-dialog-padding: 20px;
	--uap-dialog-size-xsmall: 280px;
	--uap-dialog-size-small: 400px;
	--uap-dialog-size-regular: 500px;
	--uap-dialog-size-large: 700px;
	--uap-dialog-size-xlarge: 1000px;

	// Accordion
	--uap-accordion-item-padding: 7px 12px;
	--uap-accordion-item-children-background: #f3f3f3;
	--uap-accordion-item-children-background-hover: #e3e3e3;
	--uap-accordion-border-color: var(--uap-field-border-color);
	--uap-accordion-item-li-color: #565555;

	// Tooltip
	--uap-tooltip-background: rgba(97,97,97,0.97);

	// Others

	--uap-color-gray-2: #a4a4a4;
	--uap-color-placeholder-gray: #e2e2e2;

	/* Spacing */

	--uap-spacing-between-elements-xsmall: 4px;
	--uap-spacing-between-elements-small: 8px;
	--uap-spacing-between-elements: 12px;

	--uap-spacing-between-sections-small: 20px;
	--uap-spacing-between-sections: 40px;

	--uap-border-color-gray: #e4e4e4;

	--uap-shadow-dropdown: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
	--uap-box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.1);
	--uap-box-shadow-small: 0 2px 5px 0 rgba(0,0,0,.1);

	--uap-transition: all 150ms ease-in-out;
	--uap-transition-slow: all 300ms ease-in-out;

	/* Typography */

	--uap-font-color: #3c3c3c;
	--uap-font-anchor: #0478c3;
	--uap-font-color-green: #5faa51;
	--uap-font-color-warning: #eb5e37;
	--uap-font-color-success: #2e9788;
	--uap-font-color-error: var(--uap-color-red);

	--uap-font-color-secondary: #676b6f;
	--uap-font-color-secondary-less-important: #b4b4b4;
	--uap-font-background-color: #48ad35;
	--uap-font-background-color-secondary: #e4e4e4;

	--uap-font-family--header: "Nunito", -apple-system, system-ui, blinkmacsystemfont, "Segoe UI", roboto, "Helvetica Neue", arial, sans-serif;

	--uap-font-size--body-xxsmall: 12px;
	--uap-line-height--body-xxsmall: 18px;

	--uap-font-size--body-xsmall: 14px;
	--uap-line-height--body-xsmall: 20px;

	--uap-font-size--body-small: 15px;
	--uap-line-height--body-small: 24px;

	--uap-font-size--body-regular: 16px;
	--uap-line-height--body-regular: 26px;

	--uap-font-size--body-large: 18px;
	--uap-line-height--body-large: 28px;

	--uap-font-size--header-xxsmall: 18px;
	--uap-line-height--header-xxsmall: 28px;

	--uap-font-size--header-xsmall: 20px;
	--uap-line-height--header-xsmall: 24px;

	--uap-font-size--header-small: 24px;
	--uap-line-height--header-small: 28px;

	--uap-font-size--header-regular: 28px;
	--uap-line-height--header-regular: 32px;

	--uap-font-size--header-large: 36px;
	--uap-line-height--header-large: 40px;

	--uap-font-size--header-xlarge: 44px;
	--uap-line-height--header-xlarge: 48px;

	/* Scrollbar */

	--uap-scrollbar-width: 10px;
	--uap-scrollbar-background: rgba(0, 0, 0, 0.09);
	--uap-scrollbar-thumb-background: rgba(0, 0, 0, 0.3);

	/* Item status */

	--uap-item-status-completed: #67ec73;
	--uap-item-status-completed-do-nothing: #67ec73;
	--uap-item-status-cancelled: #67ec73;
	--uap-item-status-skipped: #67ec73;
	--uap-item-status-completed-with-notice: #67ec73;
	
	--uap-item-status-not-completed: #e75c5c;
	--uap-item-status-completed-with-errors: #e75c5c;

	--uap-item-status-completed-awaiting: #f5d927;
	--uap-item-status-in-progress: #f5d927;
	--uap-item-status-scheduled: #f5d927;
}

/**
 * Helper
 */

.uap-hidden {
	display: none !important;
}

.uap-disable-scroll {
	overflow: hidden !important;
}

/**
 * Links
 */

.uap {
	a {
		color: var(--uap-font-anchor);
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
}

/**
 * Lists
 */

.uap {
	ul, ol {
		margin-left: 0;
		list-style-position: inside;
	}

	ul {
		list-style-type: disc;
	}

	.uap-list-no-bullets {
		list-style: none !important;
	}
}

/**
 * Heading
 */

.uap {
	h1, h2, h3, h4, h5, h6 {
		margin-bottom: 0;
		padding: 0;
	}

	h1 {
		font-size: var(--uap-font-size--header-xlarge);
		line-height: var(--uap-line-height--header-xlarge);
	}

	h2 {
		font-size: var(--uap-font-size--header-large);
		line-height: var(--uap-line-height--header-large);
	}

	h3 {
		font-size: var(--uap-font-size--header-regular);
		line-height: var(--uap-line-height--header-regular);
	}

	h4 {
		font-size: var(--uap-font-size--header-small);
		line-height: var(--uap-line-height--header-small);
	}

	h5 {
		font-size: var(--uap-font-size--body-large);
		line-height: var(--uap-line-height--body-large);
	}
}

/**
 * Spacing, experimental
 */

.uap-spacing-top {
	margin-top: 15px !important;

	&--none {
		margin-top: 0 !important;
	}

	&--big {
		margin-top: 20px !important;
	}

	&--small {
		margin-top: 10px !important;
	}
}

.uap-spacing-bottom {
	margin-bottom: 15px !important;

	&--none {
		margin-bottom: 0 !important;
	}

	&--small {
		margin-bottom: 10px !important;
	}
}

.uap-spacing-right {
	margin-right: 15px !important;

	&--none {
		margin-right: 0 !important;
	}

	&--big {
		margin-right: 20px !important;
	}

	&--small {
		margin-right: 10px !important;
	}

	&--xsmall {
		margin-right: 5px !important;
	}
}