/*
** Tables
** uap-logs-recipe-table
** uap-logs-trigger-table
** uap-logs-action-table
**
** Rows
** Recipe:
** Completed with errors:   uap-logs-row-recipe-status--completed-with-errors
** Action:
** Completed with errors:   uap-logs-row-action-status--completed-with-errors
** Not completed:           uap-logs-row-action-status--not-completed
*/

:root {
    /* Column sizes */
    --uap-logs-column-status: 215px;
    --uap-logs-column-completion-date: 215px;
    --uap-logs-column-runs-number: 112px;
    --uap-logs-column-buttons: 100px;
    --uap-logs-column-button: 70px;
}

/* Tables */
/* Recipe table */
.uap-logs-recipe-table {
    tbody tr {
        transition: background-color 150ms ease-in-out;

        &:hover {
            background-color: rgba(var(--uap-color-blue-rgb),.1);
        }
    }

    @media screen and (min-width: 1200px){
        #recipe_completed {
            width: var(--uap-logs-column-status);

            @media screen and (max-width: 1370px){
                width: 195px;
            }

        }
    
        #recipe_date_time {
            width: var(--uap-logs-column-completion-date);

            @media screen and (max-width: 1370px){
                width: 185px;
            }

        }
    
        #run_number {
            width: var(--uap-logs-column-runs-number);
        }
        
        #display_name {
            width: auto;
        }
    
        #actions {
            width: var(--uap-logs-column-buttons);
        }
    }
}

/* Trigger table */
.uap-logs-trigger-table {
    tbody tr {
        transition: background-color 150ms ease-in-out;

        &:hover {
            background-color: rgba(var(--uap-color-blue-rgb),.1);
        }
    }

    @media screen and (min-width: 1200px){
        #trigger_date {
            width: var(--uap-logs-column-completion-date);

            @media screen and (max-width: 1370px){
                width: 155px;
            }

        }
    
        #recipe_completed {
            width: var(--uap-logs-column-status);

            @media screen and (max-width: 1370px){
                width: 140px;
            }

        }

        #recipe_title {
            width: 400px;

            @media screen and (max-width: 1370px){
                width: 190px;
            }
        }
    
        #recipe_run_number {
            width: var(--uap-logs-column-runs-number);
        }
    
        #display_name {
            width: auto;
        }
    
        #actions {
            width: var(--uap-logs-column-button);
        }
    }
}

/* Action table */
.uap-logs-action-table {
    tbody tr {
        transition: background-color 150ms ease-in-out;

        &:hover {
            background-color: rgba(var(--uap-color-blue-rgb),.1);
        }
    }

    @media screen and (min-width: 1200px){
        #action_completed {
            width: 140px;
        }

        #error_message {
            width: 250px;
        }
        
        #action_date {
            width: 155px;
        }

        #recipe_title {
            width: 250px;
        }
    
        #recipe_completed {
            width: 140px;
        }
    
        #recipe_run_number {
            width: var(--uap-logs-column-runs-number);
        }
    
        #display_name {
            width: auto;

            @media screen and (max-width: 1370px){
                width: 190px;
            }

        }
    
        #actions {
            width: var(--uap-logs-column-button);
        }
    }
}

/* Exceptions */
.uo-recipe_page_uncanny-automator-recipe-activity-details {
    
    .uap-logs-row-recipe-status--completed-with-errors.uap-logs-row-trigger-status:nth-child(odd) {
        background-color: #f6f7f7 !important;
    }
    
    .uap-logs-row-recipe-status--completed-with-errors.uap-logs-row-trigger-status:nth-child(even) {
        background-color: #fff !important;
    }

    .uap-logs-row-recipe-status--completed-with-errors.uap-logs-row-trigger-status:hover {
        background-color: rgba(var(--uap-color-blue-rgb),.1);
    }
}

/* Cells */
/* Recipe name */
.uap-log-table {
    &-recipe-container {
        display: flex;
        gap: 8px;
        flex-direction: column;
    }

    &__recipe-name {
        font-size: var(--uap-font-size--body-xsmall);
        line-height: var(--uap-line-height--body-xsmall);
        font-weight: 600;
        color: var(--uap-font-color) !important;
        text-decoration: none;

        transition: var(--uap-transition) !important;

        @media screen and (max-width: 1370px){
            font-size: 13px;
        }

        .uap-logs-trigger-table &,
        .uap-logs-action-table & {
            font-weight: 400;
        }

        &:hover {
            text-decoration: underline;
            color: var(--uap-color-blue) !important;
        }

        .uap-logs tr:hover & {
            color: var(--uap-color-blue) !important;
        }
    }

    &__recipe-integrations {
        display: flex;
        align-items: center;
        gap: 8px;
    }

    &-recipe-integration {
        &-triggers,
        &-actions {
            display: flex;
            gap: 8px;

            uo-icon {
                --uap-icon-size: var(--uap-font-size--body-xsmall);

                @media screen and (max-width: 1370px){
                    --uap-icon-size: 13px;
                }
            }
        }
    }

}

/* Recipe status */
.uap-logs-row-recipe-status--completed-with-errors {
    background-color: #FFF1F1 !important;

    &:hover {
        background-color: #ffeaea !important;
    }

    td {
        border-top: 1px solid #F1D6D6;
        border-bottom: 1px solid #F1D6D6;

        .uo-recipe_page_uncanny-automator-recipe-activity-details & {
            border-top: 0;
            border-bottom: 0;
        }
    }

    &+ & td {
        border-top: 0;
    }
}

.uap-logs-recipe-status {
    display: flex;
    align-items: center;
    gap: 8px;
    
    font-size: var(--uap-font-size--body-xsmall);
    line-height: var(--uap-line-height--body-xsmall);
    font-weight: 400;
    color: var(--uap-font-color);

    @media screen and (max-width: 1370px){
        font-size: 13px;
    }

    &::before {
        flex: 0 0 auto;

        content: "";
        width: 10px;
        height: 10px;
        border-radius: 150%;
    }
    
    &--completed::before {
        background-color: var(--uap-item-status-completed);
    }

    &--completed-with-notice::before {
        background-color: var(--uap-item-status-completed-with-notice);
    }

    &--completed-do-nothing::before {
        background-color: var(--uap-item-status-completed-do-nothing);
    }

    &--completed-with-errors::before {
        background-color: var(--uap-item-status-completed-with-errors);
    }

    &--not-completed::before {
        background-color: var(--uap-item-status-not-completed);
    }

    &--in-progress::before {
        background-color: var(--uap-item-status-in-progress);
    }

    &--completed-awaiting::before {
        background-color: var(--uap-item-status-completed-awaiting);
    }
}

/* Recipe complation date and time */
.uap-logs-complation-date {
    font-size: var(--uap-font-size--body-xsmall);
    line-height: var(--uap-line-height--body-xsmall);
    font-weight: 400;
    color: var(--uap-font-color);

    @media screen and (max-width: 1370px){
        font-size: 13px;
    }

    &__time {
        color: var(--uap-font-color-secondary);
    }
}

/* Recipe and user runs */
.uap-logs-run-number {
    display: flex;
    gap: 20px;
    font-size: var(--uap-font-size--body-xsmall);
    line-height: var(--uap-line-height--body-xsmall);
    font-weight: 400;
    color: var(--uap-font-color);

    @media screen and (max-width: 1370px){
        font-size: 13px;
    }

    &--recipe {}

    &--user {
        display: flex;
        gap: 5px;

        color: var(--uap-font-color-secondary);

        .uo-icon {
            font-size: var(--uap-font-size--body-xsmall);
            line-height: var(--uap-line-height--body-xsmall);
        }
    }
}

/* User */
.uap-logs-user {
    display: flex;
    align-items: center;
    gap: 8px;
    max-width: 250px;

    @media screen and (max-width: 1370px){
        max-width: 170px;
    }

    .uap-logs-action-table & {
        @media screen and (max-width: 1370px){
            max-width: 170px;
        }
    }

    &__avatar-container {
        flex: 0 0 auto;
        width: 32px;
        height: 32px;
    }

    &__avatar {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 150%;
    }

    &__info {
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }

    &__display-name {
        font-size: var(--uap-font-size--body-xsmall);
        line-height: var(--uap-line-height--body-xsmall);
        color: var(--uap-font-color) !important;

        @media screen and (max-width: 1370px){
            font-size: 13px;
        }

        &--deleted-user {
            font-size: var(--uap-font-size--body-xsmall);
            line-height: var(--uap-line-height--body-xsmall);
            font-weight: 500;
            color: var(--uap-font-color) !important;

            @media screen and (max-width: 1370px){
                font-size: 13px;
            }
        }

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        transition: var(--uap-transition) !important;

        &:hover {
            text-decoration: underline;
            color: var(--uap-color-blue) !important;
        }

        .uap-logs tr:hover & {
            color: var(--uap-color-blue) !important;
        }
    }

    &__email {
        font-size: 13px;
        line-height: var(--uap-line-height--body-xsmall);

        color: var(--uap-font-color-secondary);

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    &__id {
        font-size: 13px;
        line-height: var(--uap-line-height--body-xsmall);

        color: var(--uap-font-color-secondary);
    }
}

/* Buttons */
.uap-logs-action-buttons {
    display: flex;
    justify-content: flex-end;
    min-height: 27px;

    .uap-logs-action-button {
        opacity: 0;
        transition: opacity 150ms ease-in-out;

        --uap-font-size--body-xsmall: var(--uap-font-size--body-regular);

        &--details {
            opacity: .4;
        }

        // .uap-logs-row-recipe-status--completed-with-errors & {
        //     display: block;
        // }

        .uap-logs tr:hover & {
            opacity: 1;
        }

        &--delete {
            --uap-button-style-custom-color: var(--uap-font-color-warning);
            --uap-button-style-custom-hover-color: var(--uap-font-color-warning);
        }    
    }

}

/* Trigger name */
.uap-logs-table-trigger {
    display: flex;
    flex-direction: column;
    gap: 2px;

    .uap-logs-table__trigger-name {
        font-size: var(--uap-font-size--body-xsmall);
        line-height: var(--uap-line-height--body-xsmall);
        font-weight: 600;

        @media screen and (max-width: 1370px){
            font-size: 13px;
        }

        color: var(--uap-font-color);
    }

    .uap-logs-table__trigger-run-number {
        font-size: 13px;
        line-height: var(--uap-line-height--body-xsmall);
        color: var(--uap-font-color);
    }
}

/* Action name */
.uap-logs-table-action {
    display: flex;
    flex-direction: column;
    gap: 2px;

    .uap-logs-table__action-name {
        font-size: var(--uap-font-size--body-xsmall);
        line-height: var(--uap-line-height--body-xsmall);
        font-weight: 600;

        @media screen and (max-width: 1370px){
            font-size: 13px;
        }

        color: var(--uap-font-color);
    }
}

/* Action status */
.uap-logs-row-action-status--completed-with-errors {
    background-color: #FFF1F1 !important;

    td {
        border-top: 1px solid #F1D6D6;
        border-bottom: 1px solid #F1D6D6;
    }

    &+ & td {
        border-top: 0;
    }
}

.uap-logs-row-action-status--not-completed {
    background-color: #FFF1F1 !important;

    td {
        border-top: 1px solid #F1D6D6;
        border-bottom: 1px solid #F1D6D6;
    }

    &+ & td {
        border-top: 0;
    }
}

.uap-logs-action-status {
    display: flex;
    align-items: center;
    gap: 8px;
    
    font-size: var(--uap-font-size--body-xsmall);
    line-height: var(--uap-line-height--body-xsmall);
    font-weight: 400;
    color: var(--uap-font-color);

    @media screen and (max-width: 1370px){
        font-size: 13px;
    }

    &::before {
        flex: 0 0 auto;

        content: "";
        width: 10px;
        height: 10px;
        border-radius: 150%;
    }
    
    /* Green */
    &--completed::before {
        background-color: var(--uap-item-status-completed);
    }

    &--completed-do-nothing::before {
        background-color: var(--uap-item-status-completed-do-nothing);
    }

    &--completed-with-notice::before {
        background-color: var(--uap-item-status-completed-with-notice);
    }
    
    &--cancelled::before {
        background-color: var(--uap-item-status-cancelled);
    }

    &--skipped::before {
        background-color: var(--uap-item-status-skipped);
    }

    /* Red */
    &--not-completed::before {
        background-color: var(--uap-item-status-not-completed);
    }

    &--completed-with-errors::before {
        background-color: var(--uap-item-status-completed-with-errors);
    }

    /* Yellow */
    &--in-progress::before {
        background-color: var(--uap-item-status-in-progress);
    } 

    &--scheduled::before {
        background-color: var(--uap-item-status-scheduled);
    }
    
    &--completed-awaiting::before {
        background-color: var(--uap-item-status-completed-awaiting);
    }
}