// Import breakpoints
@import '@automator/utilities/breakpoint/style.scss';

/**
 * Variables
 *
 * These variables apply only to the Integrations (archive and single) pages.
 */

:root {
    --uap-integrations-banner-breadcrumbs: #8898aa;
}

.uap-integrations {
    background: #fff;

    font-family: var(--uap-font-family);

	/**
    * Archive - Banner
    */

	&-banner {
        background-image: url( 'img/integrations-page-banner-background.png' ); 
        background-size: cover;
        background-repeat: no-repeat;
        background-color: #fff;

        padding: 30px 0 0 30px;

        &__breadcrumbs {
            font-size: var(--uap-font-size--body-small);
            font-weight: 400 !important;
            color: var(--uap-integrations-banner-breadcrumbs) !important;

            transition: var(--uap-transition);

            text-decoration: none;
            & span {
                margin-right: var(--uap-spacing-between-elements);
            }
        }

        &__breadcrumbs:hover {
            color: var(--uap-color-blue) !important;
        }

        &__title {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-regular);
            font-weight: 700;
            line-height: var(--uap-line-height--header-regular);

            color: var(--uap-font-color);

            margin: 30px 0;
        }

        &-items {
            padding-bottom: 60px;
        }

        &-item {
            display: flex;
            align-items: center;

            &__icon {
                width: 27px;

                font-size: var(--uap-font-size--header-small) !important;
                color: var(--uap-color-blue);

                margin-right: var(--uap-spacing-between-elements);
            }

            &__text {
                font-size: var(--uap-font-size--header-xsmall);
                font-weight: 400;
                line-height: var(--uap-line-height--header-xsmall);
            }
        }

        &-item:not(:last-child) {
            margin-bottom: var(--uap-spacing-between-sections-small);
        }
    }

	/**
     * All sections
     */

    &-search,
    &-collections,
    &-all {
        padding: 0 30px;
        background-color: #fff;
    }

    /**
     * Archive - Search
     */

    &-search {
        position: sticky;
        -webkit-position: sticky;
        top: 32px;
        z-index: 100000;

        padding: 20px 30px;
        margin-bottom: 30px;
    }

    &-search-sections {
        /* display: flex !important; */
        align-items: center;
        margin: 0;
        padding: 15px 0 0 0;

        /* Temporary change */
        display: none;

        & li {
            margin: 0 25px 0 0;

            & a {
                text-decoration: none;
                font-size: var(--uap-font-size--body-small);
                line-height: var(--uap-line-height--body-small);
                color: var(--uap-font-color-secondary);
                text-transform: uppercase;

                transition: var(--uap-transition);
            }

            & a:hover {
                color: var(--uap-color-blue) !important;
            }
        }
    }

	/**
     * Archive - Integrations sections
     */

    &-collection {
        padding-bottom: 80px;
    }

	&-collections {

        &__title {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-regular);
            line-height: var(--uap-line-height--header-regular);
            font-weight: 600;
            color: var(--uap-font-color);
            padding: 0 0 5px;
            margin: 0;
        }

        &__subtitle {
            font-family: var(--uap-font-family);
            font-size: var(--uap-font-size--body-large);
            line-height: var(--uap-line-height--body-large);
            font-weight: 400;
            color: var(--uap-font-color-secondary);
            padding: 0 0 40px;
            margin: 0;
        }

        &-integrations {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 50px;

            width: 100%;

            @media screen and (min-width: 1700px) {
                grid-template-columns: 1fr 1fr 1fr 1fr;
            }

            @media screen and (max-width: 1200px) {
                grid-template-columns: 1fr 1fr;
            }

            @media screen and (max-width: 780px) {
                grid-template-columns: 1fr;
            }
        }

        &-integration {
            text-decoration: none;

            outline: 0 !important;
            box-shadow: none !important;

            &:hover {
                text-decoration: none !important;
            }

            &-content {
                display: flex;
                align-items: flex-start;
            }

            &__icon-container {
                flex: 0 0 auto;
            }

            &__info-container {
                flex: 1 1 auto;
                padding: 0 0 0 25px;
            }

            &__icon {
                position: relative;

                background: #fff;
                border: 1px solid var(--uap-border-color-gray);
                border-radius: 50%;
                box-shadow: var(--uap-box-shadow-small);

                width: 65px;
                height: 65px;

                transition: var(--uap-transition);

                & img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%,-50%);

                    display: block;
                    height: 36px;
                    width: 36px;
                }
            }

            &__name {
                font-family: var(--uap-font-family--header);
                font-size: var(--uap-font-size--body-large);
                line-height: var(--uap-line-height--body-large);
                font-weight: 600;
                color: var(--uap-font-color);
            }

            &--has-tags {}

            &__tags {
                display: flex;
                align-items: center;
            }

            &__tag {
                font-size: var(--uap-font-size--body-xxsmall);
                line-height: 1;
                font-weight: 400;
                text-transform: uppercase;

                padding: 5px 7px;
                margin: 6px 10px 12px 0;

                border-radius: 2px;
            }

            &__tag-pro {
                background-color: var(--uap-font-background-color);
                color: #fff;
            }

            &__tag-built-in,
            &__tag-installed {
                background-color: var(--uap-font-background-color-secondary);
                color: var(--uap-font-color);
            }

            &__description {
                font-size: var(--uap-font-size--body-small);
                line-height: var(--uap-line-height--body-small);
                font-weight: 400;
                color: var(--uap-font-color-secondary);
                opacity: .7;
            }

            &--requieres-pro {
                position: relative;

                font-size: var(--uap-font-size--body-small);
                line-height: 1;
                font-weight: 400;
                color: var(--uap-font-background-color);
                
                padding: 10px 0 0 18px;

                & span {
                    left: 0 !important;
                    font-weight: 400 !important;
                    padding-top: 10px;
                }
            }
        }

        &-no-results {
            display: none;

            font-size: var(--uap-font-size--body-small);
            line-height: var(--uap-line-height--body-small);
        }
    }
}

.uap-integration {
    font-family: var(--uap-font-family);

    /**
    * Single - Banner
    */

    &-banner {

        padding: 30px 30px 0;

        &__breadcrumbs {
            font-size: var(--uap-font-size--body-small);
            font-weight: 400 !important;

            transition: var(--uap-transition);

            text-decoration: none;

            & span {
                margin-right: var(--uap-spacing-between-elements);
            }
        }

        &-info-container {
            display: flex;
            justify-content: center;
            padding: 50px 0;
        }

        &-info {
            display: flex;
            align-items: flex-start;
            
            width: 100%;
            max-width: 800px;

            &__icon-container {
                flex: 0 0 auto;
            }

            &__info-container {
                flex: 1 1 auto;
                padding-left: 30px;
            }

            &__icon {
                position: relative;

                background: #fff;
                border: 1px solid var(--uap-border-color-gray);
                border-radius: 50%;
                box-shadow: var(--uap-box-shadow-small);

                width: 120px;
                height: 120px;

                transition: var(--uap-transition);

                & img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%,-50%);

                    display: block;
                    height: 75px;
                    width: 75px;
                }
            }

            &__name {
                font-family: var(--uap-font-family--header);
                font-size: var(--uap-font-size--header-regular);
                line-height: var(--uap-line-height--header-regular);
                font-weight: 600;
                color: var(--uap-font-color);
            }

            &__description {
                margin-top: 10px;
                font-size: var(--uap-font-size--body-large);
                line-height: var(--uap-line-height--body-large);
                font-weight: 400;
                color: var(--uap-font-color-secondary);
            }
        }
    }

    &-banner-menu {
        padding: 0;
        background-color: #fff;

        &-sections {
            display: flex !important;
            align-items: center;
            margin: 0;
            padding: 15px 0;
            border: 1px solid var(--uap-font-background-color-secondary);
        
            & li {

                margin: 0;

                & a {
                    margin: 0 25px 0 0;

                    font-size: var(--uap-font-size--body-small);
                    line-height: var(--uap-line-height--body-small);
                    font-weight: 400;
                    color: var(--uap-font-color-secondary);
                    text-transform: uppercase;
                    text-decoration: none;
                }   
            }  

            & li:first-child {
                margin-left: 30px !important;
            }   
        }
    }

    &-content {
        display: flex;

        background-color: #fff;

        padding: 50px 30px 0 0;

        &__left {
            width: 100%;
            flex: 1 1 auto;
        }

        &__right {
            width: 300px;

            flex: 0 0 auto;
            padding-left: 30px;
            padding-bottom: 30px;

            border-left: 1px solid var(--uap-font-background-color-secondary);            
        }
    }

    /**
     * Single - Items
     */

    &-items {
        display: flex;
        flex-direction: row;
        padding: 0 30px 60px;

        &-col {
            width: 50%;
        }

        &-col:first-child {
            margin-right: 10px;
        }

        &-col:last-child {
            margin-left: 10px;
        }

        &-col-container {
            @media screen and (min-width: $breakpoint_mobile){
                margin-left: -20px;
            }
        }

        &__title-container {
            display: flex;
        }

        &__title {
            width: 100%;
            position: relative;

            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-xsmall);
            line-height: var(--uap-line-height--header-xsmall);
            font-weight: 700;
            color: var(--uap-font-color);

            padding-bottom: 15px;
            margin-left: 0;
        }

        &__title--triggers {
            padding-bottom: 5px !important;
            margin-left: 20px !important;   
        }

        &__title-tooltip {
            flex: 0 0 auto;
            margin-top: 2px;
            margin-right: 10px;
            width: 19px;
            height: 19px;

            font-size: 19px !important;
            cursor: help;
        }

        &-container {}

    }

    &-item {
        border-radius: var(--uap-border-radius);

        &-header {}

        .uap-accordion-item__toggle {
            padding: 10px 20px 0 !important;
        }

        .uap-accordion-item__toggle:before,
        .uap-accordion-item__toggle:after {
            right: 20px;
            top: 25px;
        }

        &__sentence {
            position: relative;
            width: 100%;

            font-size: var(--uap-font-size--body-large);
            line-height: var(--uap-line-height--body-large);
            font-weight: 400;
            color: var(--uap-font-color);

            padding-right: 20px;
            padding-top: 4px;
        }

        &__sentence--pro {
            position: relative;
            text-indent: 43px;
        }

        &__sentence--pro:before {
            content: attr(pro-tag);
            position: absolute;
            left: 0;
            top: 8px;
            display: inline-block;

            font-size: var(--uap-font-size--body-xxsmall);
            line-height: 1;
            font-weight: 600;
            color: #fff;

            padding: 3px 6px;
            
            border-radius: 150px;
            background: #323232;

            text-indent: 0;
        }

        &-tokens { 
            font-size: var(--uap-font-size--body-small);

            padding: var(--uap-box-padding);
            background: transparent;
            
            transition: var(--uap-transition-slow);
            
            &__title-container {
                display: flex;
            }

            &__title {
                position: relative;
                font-weight: 500;
            }

            &__title-tooltip {
                margin-top: 5px;
                margin-left: 4px;
                width: 16px;
                height: 16px;
                cursor: help;
            }

            &__list {
                margin-left: 30px;
                list-style-type: disc;
            }
        }

        &-token {
            font-size: var(--uap-font-size--body-small);
            line-height: var(--uap-line-height--body-small);
            font-weight: 400;
            color: var(--uap-font-color);
        }

        /* Style when the integration doesn't have items */
        &--empty {
            font-size: var(--uap-font-size--body-large);
            line-height: var(--uap-line-height--body-large);
            font-style: italic;
            color: var(--uap-font-color-secondary);
        }
    }

    /**
     * Single - Recipes
     */

    &-recipes {
        padding: 60px 30px;

        &__title {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-regular);
            line-height: var(--uap-line-height--header-regular);
            font-weight: 600;
            color: var(--uap-font-color);
            padding: 0 0 5px;
            margin: 0;
        }

        &__subtitle {
            font-size: var(--uap-font-size--body-large);
            line-height: var(--uap-line-height--body-large);
            font-weight: 400;
            color: var(--uap-font-color-secondary);
            padding: 0 0 40px;
            margin: 0;
        }

        &__items {
            display: flex;

            width: 100%;
        }

        &__item {
            width: 33%;
            
            padding: var(--uap-box-padding);
            margin-right: 15px;

            background-color: #fff;
            
            border-radius: var(--uap-border-radius);
            box-shadow: var(--uap-box-shadow);

        }
    }

    &-recipe {
        &__name {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--body-large);
            line-height: var(--uap-line-height--body-large);
            font-weight: 600;
            color: var(--uap-font-color);
            padding: 0;
            margin: 0;
        }

        &__require {
            margin-top: 15px;
        }

        &__text {
            font-size: var(--uap-font-size--body-regular);
            line-height: var(--uap-line-height--body-regular);
            font-weight: 400;
            color: var(--uap-font-color);
            padding: 0;
            margin: 0;
        }

        &__integrations {
            margin-top: 15px;
        }

        &-integration {
            display: flex;
            align-items: center;

            &__icon-container {
                flex: 0 0 auto;
            }

            &__icon {
                position: relative;
                
                background: #fff;
                border: 1px solid var(--uap-font-background-color-secondary);
                border-radius: 50%;
                box-shadow: var(--uap-box-shadow-small);
                left: 0;
                top: 0;
                height: 30px;
                width: 30px;

                & img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: calc( 100% - 12px );
                    transform: translate(-50%,-50%);
                }
            }

            &__info-container {
                flex: 1 1 auto;
            }

            &__info {
                font-family: var(--uap-font-family--header);
                font-size: var(--uap-font-size--body-large);
                line-height: var(--uap-line-height--body-large);
                font-weight: 600;
                color: var(--uap-font-color);

                padding-left: 10px;
            }
        }

        &-integration:not(:last-child){
            margin-bottom: 15px;
        }
    }

    /**
     * Single - Sidebar
     */

    &-sidebar {

        &__title {
            font-family: var(--uap-font-family--header);
            font-size: var(--uap-font-size--header-xsmall);
            line-height: var(--uap-line-height--header-xsmall);
            font-weight: 700;
            color: var(--uap-font-color);

            padding-bottom: 15px;
        }

        &-requires {
            width: 100%;
            margin-bottom: 30px;
            border-radius: var(--uap-border-radius);
            box-shadow: var(--uap-box-shadow-small);

            &__row {
                position: relative;
                padding: 15px 15px 15px 65px;
            }

            &__icon {

                position: absolute;
                background: #fff;
                border: 1px solid var(--uap-font-background-color-secondary);
                border-radius: 50%;
                box-shadow: var(--uap-box-shadow-small);
                left: 15px;
                top: 15px;
                height: 35px;
                width: 35px;

                & img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: calc( 100% - 12px );
                    transform: translate(-50%,-50%);
                }
            }

            &__name {
                font-family: var(--uap-font-family--header);
                font-size: var(--uap-font-size--body-large);
                line-height: var(--uap-line-height--body-large);
                font-weight: 600;
                color: var(--uap-font-color);

                margin-top: 2px;
                padding-bottom: 7px;
            }

            &__button {
                position: relative;
                
                border: 1px solid;
                border-radius: var(--uap-border-radius);
                box-shadow: var(--uap-box-shadow-small);
                
                cursor: pointer;
                display: inline-block;
                
                font-size: var(--uap-font-size--body-xsmall);
                font-weight: 500;
                line-height: var(--uap-line-height--body-xsmall);
                outline: none;
                text-decoration: none;
                text-align: center;
                
                transition: var(--uap-transition);
                user-select: none;
                vertical-align: middle;
                white-space: nowrap;
                
                padding: 2px 7px;
            }
        }

        &__links {
            display: flex;
            flex-direction: column;
        }

        &__link {
            font-size: var(--uap-font-size--body-regular);
            line-height: var(--uap-line-height--body-regular);
            font-weight: 400 !important;
            color: var(--uap-font-color) !important;

            transition: var(--uap-transition);

            outline: none;
            text-decoration: none;
        }

        &__link:not(:last-child) {
            margin-bottom: 3px;
        }
    }
}

/**
 * WP exceptions
 */

.uo-recipe_page_uncanny-automator-integrations {
    #wpcontent {
        padding-left: 0;
    }
}