.uap-notifications {

	margin: 0 25px 25px 25px;
	padding: 0;
	border: 1px solid var(--uap-border-color-gray);
	color: var(--uap-font-color);
	border-radius: 8px;
    background: #fff;

	h3 {
		color: var(--uap-font-color);
		font-family: var(--uap-font-family--header);
	}
	p {
		color: var(--uap-font-color);
		font-size: var(--uap-font-size--body-small);
		font-weight: 400;
		line-height: var(--uap-line-height--body-small);
		margin: 5px 0 1em 0;
	}
	span.notifications-count {
		background: #f16666;
		color: #f16666;
		display: block;
		position: absolute;
		top: 8px;
		left: 30px;
		height: auto;
		text-align: center;
		border: 2px solid #fff;
		border-radius: 20px;
		padding: 0px;
		font-size: 10px;
		width: 8px;
		line-height: 8px;
    }
    
    h3.uap-notifications__title {
        position: relative;
        border-bottom: 1px solid var(--uap-border-color-gray);
        margin: 0;
        font-size: 16px;
        padding: 5px 20px;
        display: flex;
        align-items: center;
        color: var(--uap-font-color);
        width: 100%;
        box-sizing: border-box;
        .dashicons-bell {
            margin-right: 10px;
        }
    }
    .uap-notifications-list {
        padding: 0;
        margin: 0;
        &__item{
            list-style: none;
            padding: 10px 20px 20px 20px;
            margin: 0;
            display: none;
            &.active {
                display: block;
            }
        }
        &__item-content {
            p {
                color: var(--uap-font-color-secondary);
            }
            max-width: 90%;
        }
    }
    
    .uap-settings-header__title.uap-notifications__title {
        font-size: 22px;
    }

    .uap-notifications-list__item {
        &-title {
            margin: 0 0 0 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            h3 {
                padding: 0;
                margin: 0;
                font-size: 18px;
            }
        }
        &-actions {
            display: flex;
            align-items: center;
            justify-content: space-between;
            &__links {
                uo-button {
                    &:first-child {
                        margin-right: 5px;
                    }
                }
            }
        }
    }
   
}

body {
    &.post-type-uo-recipe {
        .uap-notifications {
            margin: 20px 20px 0 0;
        }
        &.uo-recipe_page_uncanny-automator-config{
            .uap-notifications {
                margin: 0 25px 25px 25px;
            }
        }
    }
    &.uo-recipe_page_uncanny-automator-tools {
        .uap-notifications {
            margin: 15px 0;
        }
    }
    &.uo-recipe_page_uncanny-automator-dashboard {
        .uap-notifications {
            margin: 0 0 20px 0;
        }
    }
}
